@import '../helpers/helpers-all.scss';
@import '../helpers/helpers-all.scss';
@import '../helpers/mixins/typography/typography.scss';
.executive_team {
	background-color: $yellow;
	border-top: 0.7rem solid $black;

	@include bp($point_2, min) {
		padding-top: 5rem;
		padding-bottom: 6.6rem;
	}

	@include bp($point_3) {
		padding-top: 3.1rem;
		padding-bottom: 8.8rem;
	}
}

.executive_team__title {
	@include h3;
	color: #222222;
	text-transform: uppercase;

	@include bp($point_2, min) {
		margin-bottom: 4.7rem;
	}

	@include bp($point_3) {
		font-size: 5.2rem;
		margin-bottom: 3.1rem;
	}
}

.executive_team__list {
	// display: flex;
	// flex-wrap: wrap;
	overflow: visible !important;

	@include bp($point_1, min) {
		margin-right: calc((100vw - #{$page_width}) / -2);
	}
}

.executive_team__item {
	@include bp($point_2, min) {
		display: flex !important;
		width: 65rem !important;
		padding-right: 7rem;
	}

	@include bp($point_3) {
		width: 32.6rem !important;
		max-width: calc(100vw - var(--gl-indent) * 2);
		padding-right: 3.6rem;
	}

	/* 
	@include bp($point_1, min) {
		margin: 0 0 4rem 0;
	}

	@include bp($point_1) {
		max-width: 30rem;
		flex-direction: column;
	}

	@include bp($point_3) {
		max-width: none;
		min-width: 291px;
		margin: 0 0 4rem 0;
	} */
}

.executive_team__item_img_wrapper {
	@include bp($point_2, min) {
		width: 52%;
		padding-right: 1rem;
	}

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 2rem;
	}
}

.executive_team_gallery__pic {
	position: relative;
	display: block;
	width: 100%;

	&:before {
		content: '';
		display: block;
		padding-bottom: 133.33%;
	}
}

.executive_team_gallery__img {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
}

.executive_team__item_text_content {
	@include bp($point_2, min) {
		width: 48%;
	}
}

.executive_team__item_head {
	@include bp($point_2, min) {
		margin-bottom: 1;
	}

	@include bp($point_3) {
		font-size: 5.2rem;
		margin-bottom: 1.6rem;
	}
}

.executive_team__item_title,
.executive_team__item_job {
	font-size: 6rem;
	font-weight: 400;
	line-height: 0.96;
	color: #222;
	text-transform: uppercase;
}

.executive_team__item_text__wrapper {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;

	@include bp($point_2, min) {
		min-height: 10rem;
		margin-left: 0.6rem;
	}

	&::after {
		content: '';

		display: block;
		width: 100%;

		height: 4px;

		background-color: $black;

		@include bp($point_2, min) {
			margin-top: 1.3rem;
			max-width: 24.1rem;
		}

		@include bp($point_3) {
			margin-top: 1rem;
		}
	}
}

.executive_team__item_text {
	font-size: 1.6rem;
	line-height: 1.2;
	font-family: $raleway;
	font-weight: 700;
	color: $black_3;
	text-transform: uppercase;

	opacity: 0.7;
}
